import React, { useState, useMemo, useEffect } from 'react'
import styled, { keyframes } from 'styled-components';
import { Box, Text } from 'grommet';

const TextWrapper = styled(Box)`
  position: relative;
  height: ${props => props.wrapperHeight};
  overflow: hidden;
`;
const rollOut = keyframes`
  0% {
    opacity: 0;
    transform: translateY(0);
  }
  20% {
    opacity: 1;
    transform: translateY(-50%);
  }
  80% {
    opacity: 1;
    transform: translateY(-50%);
  }
  100% {
    opacity: 0;
    transform: translateY(-100%);
  }
`;
const AnimatedText = styled(Text)`
  position: absolute;
  top: 50%; left: 0; right: 0;
  width: 100%;
  box-sizing: border-box;
  padding: 0 24px;
  text-align: center;
  opacity: 0;
  transform: translateY(-50%);
  animation: 5s ${props => props.index * 5}s ${rollOut} ${props => props.theme.global.easing} forwards;
`;

function Intro(props) {
  const [int, setInt] = useState();
  const [key, setKey] = useState(1);
  const texts = useMemo(() => [
    'Bir Bilgin yarmasna daha ho geldiniz',
    'Yarmamza ilk defa katlacak olanlar iin yarmamz hzlca anlatalm',
    'Yarmamz bir soru ve  ktan oluan serilerden oluuyor',
    'Soru ekrana geldikten sonra soruyu dinleyebiliyorsunuz',
    'Soru okunurken cevaplama yapamazsnz',
    'Okuma bittikten sonra belirtilen srede soruyu cevaplamalsnz',
    'Soruyu ne kadar ksa srede cevaplar iseniz o kadar fazla puan alrsnz',
    'Soruya verdiiniz cevap esnasnda geri saym sayac hangi saniyede ise puannzda o saniyedir',
    'Bir soruya bir defa cevap verebilirsiniz',
    'Verdiiniz cevab deitiremezsiniz',
    'Yarmamzda eleme yoktur.',
    'Yarmaya sonuna kadar devam ederek puan toplamaya devam edersiniz',
    'Ne kadar soruya doru cevap verirseniz o kadar fazla puan alrsnz',
    'Bylece haftann, ayn ve tm yarmalarn birincileri ortaya kacaktr',
    'Ayrca her yarmada iki tane jokeriniz bulunmaktadr',
    'Her joker bir defa kullanlabilir',
    'ift cevap jokeri ile bir sorunun iki ayr kkn iaretleyebilirsiniz',
    'Eleme jokeriyle ise yanl klardan birini eleyebilirsiniz',
    'Bylece her iki jokeri de bir yarmada birer defa kullanarak puannz korumu olursunuz',
  ], [])

  useEffect(() => {
    const newInt = setInterval(() => {
      setKey(oldKey => oldKey + 1);
    }, texts.length * 5 * 1000);
    setInt(newInt);

    return () => {
      clearInterval(int);
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [key, texts])

  return (
    <TextWrapper key={key} {...props}>
      {texts.map((text, index) => (
        <AnimatedText key={index} index={index}>{text}</AnimatedText>
      ))}
    </TextWrapper>
  )
}

Intro.defaultProps = {
  wrapperHeight: '30vh',
}

export default Intro
